import React from 'react'
import classnames from 'classnames'
// import x from 'icons/MoneyBag.svg';
// import y from 'icons/statistic.svg';
// import z from 'icons/tag.svg';
// //避免treeShaking，不然引入了没用就会被摇下去；或者我们可以require引入svg
// console.log(x,y,z);
//how to require a folder
let importAll = (requireContext: __WebpackModuleApi.RequireContext) => requireContext.keys().forEach(requireContext);
try {importAll(require.context('icons', true, /\.svg$/));} catch (error) {console.log(error);}


type IconProps={
    name?:string;
} & React.SVGAttributes<SVGElement>

const Icon = (props:IconProps) => {
    const {name,children,className,...rest}=props;
    return (
        <svg className={classnames("icon",className)} {...rest}>
           {props.name && <use xlinkHref={"#"+props.name} />}
        </svg>
    )
}

export default Icon;