import styled from "styled-components";
import React, { useState } from 'react'

const Wrapper = styled.section`
  font-size:24px;
  >ul{
    display:flex;
    /* background:#f3dde1; */
    >li{
        width:50%;
        text-align:center;
        padding:16px 0;
        position:relative;
        &.selected::after{
            content:"";
            display:block;
            height:3px;
            background-color:#fa98b8;
            position:absolute;
            bottom:0;
            width:100%; 
            left:0;
        }
    }
  }   
`
type IProps={
    value:"-"|"+";
    onChange:(value:"-"|"+")=>void;
}

const CategorySection:React.FC<IProps> = (props:IProps) => {
    const categoryMap={"-":"支出","+":"收入"};
    type keys=keyof typeof categoryMap;

    const [categoryList]=useState<keys[]>(["-","+"]);
    // const [category,SetCategory]=useState("-");//"-"支出 "+"收入
    const category=props.value;
    return (
        <Wrapper>
            <ul>
                {categoryList.map((item)=>{
                    return (
                        <li className={category===item?"selected":""}
                            onClick={()=>props.onChange(item)}
                            key={item}
                            >
                            {categoryMap[item]}
                        </li>
                    )
                })}
            </ul>
        </Wrapper> 
    )
}

export default CategorySection;