import useUpdate from "hooks/useUpdate";
import createId from "lib/createId";
import { useEffect, useState } from "react";


const useTags=()=>{
    //自定义hook，抽离出一个useState，然后把读写接口放到外面。
    const [tag, SetTag] = useState<{id:number,name:string}[]>([]);
    // console.log(tag);
    useEffect(() => {
       let localTag=JSON.parse(window.localStorage.getItem("tag")||'[]');
       if(localTag.length===0){
           localTag=[
            {id:createId(),name:"衣"},
            {id:createId(),name:"食"},
            {id:createId(),name:"住"},
            {id:createId(),name:"行"}
        ]
       }
       SetTag(localTag);
    }, []);
    // const count=useRef(0);
    // useEffect(() => {
    //     count.current+=1;
    // })
    // useEffect(() => {
    //     if(count.current>1){
    //         window.localStorage.setItem("tag",JSON.stringify(tag))
    //     }
        
    //  }, [tag]);

    useUpdate(()=>{
        window.localStorage.setItem("tag",JSON.stringify(tag))
    },tag);

    const findTag=(id:number)=>tag.filter(item=>item.id===id)[0];
    let result=-1;
    const findTagIndex=(id:number)=>{
        for(let i=0;i<tag.length;i++){
            if(tag[i].id===id){
                result=i;
                break;
            }
        }
        return result;
    }
    const updateTag=(id:number,obj:{name:string})=>{
        // //获取要改的Tag下标
        // const index=findTagIndex(id);
        // //深拷贝tags得到tagsClone
        // const tagsClone=JSON.parse(JSON.stringify(tag));
        // //把tagsClone的第index删除，换成{id:id,name:obj.name}
        // tagsClone.splice(index,1,{id:id,name:obj.name})
        // SetTag(tagsClone);

        //SetTag(tag.map(tags=>tags.id===id?{id,name:obj.name}:tag));
        SetTag(tag.map(tags=>{
            if(tags.id===id){
                return {id:id,name:obj.name}
            }else{
                return tags
            }
        }))
    }
    const deleateTag=(id:number)=>{
        //获取要删的Tag下标
        const index=findTagIndex(id);
        //深拷贝tags得到tagsClone
        const tagsClone=JSON.parse(JSON.stringify(tag));
        //把tagsClone的第index删除
        tagsClone.splice(index,1)
        SetTag(tagsClone);
    }
    const onaddTag = () => {
        const tagNmae = window.prompt("新标签的名字为：");
        if (tagNmae !== null && tagNmae!=="") {
            SetTag([...tag, {id:createId(),name: tagNmae}])
        }
    }
    const getName=(id:number)=>{
        const tagFound=tag.filter(item=>item.id===id)[0];
        return tagFound?tagFound.name:""
    }

    return {
        tag,
        SetTag,
        findTag,
        updateTag,
        findTagIndex,
        deleateTag,
        onaddTag,
        getName
    }
}

export default useTags;