import Layout from "components/Layout";
import useRecords, { IRecord } from "hooks/useRecords";
import useTags from "hooks/useTags";
import React, { ReactNode, useState } from "react";
import styled from "styled-components";
import CategorySection from "./Money/CategorySection";
import day from 'dayjs'


const CategoryWrapper = styled.div`
  background-color: #fff;
`

const Item = styled.div`
  display: flex;
  justify-content:space-between;
  background-color: #fff;
  font-size:18px;
  line-height:20px;
  padding:10px 16px;
  >.note{
    margin-right:auto;
    margin-left:16px;
    color:#999;
  }
`

const Header = styled.h3`
  font-size:18px;
  line-height:20px;
  padding:10px 16px;
`

function Statistics() {
  const [category, setCategory] = useState<"-" | "+">("-");
  const { records } = useRecords();
  const { getName } = useTags();
  const hash: { [k: string]: IRecord[] } = {}

  const selecedRecords = records.filter(r => r.category === category);
  selecedRecords.forEach((item) => {
    const key = day(item.createAt).format("YYYY-MM-DD");
    if (!(key in hash)) {
      hash[key] = [];
    }
    hash[key].push(item)
  })
  const array = Object.entries(hash).sort((a, b) => {
    if (a[0] === b[0]) { return 0 }
    if (a[0] > b[0]) { return -1 }
    if (a[0] > b[0]) { return 1 }
    return 0
  })


  return (
    <Layout>
      <CategoryWrapper>
        <CategorySection value={category} onChange={
          (value) => setCategory(value)
        } />
      </CategoryWrapper>
      {array.map(
        ([date, record]) => {
          return (<div>
            <Header>{date}</Header>
            <div>
              {record.map(item => {
                return (
                  <Item>
                    <div className="tags oneLine">
                      {item.tagIds.map(tagId => {
                        return <span key={tagId}>{getName(tagId)}</span>
                      })
                        //[span,span,span]=>[span,',',span,',',span]
                        .reduce((result, span, index, array) =>
                          result.concat(index < array.length - 1 ? [span, '，'] : [span]), [] as ReactNode[])
                      }
                    </div>
                    {item.note && <div className="note">
                      {item.note}
                    </div>}

                    <div className="amount">
                      ￥{item.amount}
                    </div>
                  </Item>
                )
              })}
            </div>
          </div>)
        }
      )}
      {/* <div>{JSON.stringify(records)}</div> */}
      <div>

      </div>
    </Layout>
  );
}

export default Statistics;