import Button from 'components/Button';
import Center from 'components/Center';
import Icon from 'components/Icon';
import Input from 'components/Input';
import Layout from 'components/Layout';
import Space from 'components/Space';
import React from 'react'
import { useParams,useHistory } from 'react-router-dom';
import styled from 'styled-components';
import useTags from 'hooks/useTags';

type Params = {
    id: string;
}

const TopBar = styled.header`
    display:flex;
    justify-content:space-between;
    padding:14px 0;
    line-height:20px;
    background-color: #fff;
    align-items:center;
`

const InputWrapper = styled.div`
    background-color: #fff;
    padding:0 16px;
    margin-top:8px;
`

const Tag: React.FC = (props) => {
    const { findTag, updateTag, deleateTag } = useTags();
    let { id: idString } = useParams<Params>();

    const tags = findTag(parseInt(idString))
    const history=useHistory();
    const onClickBack=()=>{
        history.goBack()
    }
        return (
            <Layout>
                <TopBar>
                    <Icon name="arrow_left" onClick={onClickBack}/>
                    <span>编辑标签</span>
                    <Icon />
                </TopBar>
                {tags? <div>
                    <InputWrapper>
                        <Input label="标签名" type="text" placeholder="标签名" value={tags.name}
                            onChange={(e) => {
                                updateTag(tags.id, { name: e.target.value })
                            }}
                        />
                    </InputWrapper>
                    <Center>
                        <Space />
                        <Space />
                        <Space />
                        <Button onClick={() => deleateTag(tags.id)}>删除标签</Button>
                    </Center>
                </div>:<Center>
                    <Space/>
                    <Space/>
                    <Space/>
                    <Space/>
                    <Space/>
                    <Space/>
                    <Space/>
                    Tag不存在
                    </Center>}
                
            </Layout>
        )
    }


export default Tag;