import Layout from "components/Layout";
import useRecords from "hooks/useRecords";
import React, { useState } from "react";
import styled from 'styled-components'
import CategorySection from "./Money/CategorySection";
import NotesSection from "./Money/NotesSection";
import NumberpadSection from "./Money/NumberpadSection";
import TagsSection from "./Money/TagsSection";

const MyLayOut = styled(Layout)`
    display:flex;
    flex-direction:column;
`

const CategoryWrapper=styled.div`
    background:#f3dde1;
`

const defaultFormDate = {
    tagIds: [] as number[],
    note: "",
    category: "-" as ("-" | "+"),
    amount: 0
}
function Money() {
    const [selected, SetSelected] = useState(defaultFormDate)
    const { addRecord } = useRecords();
    type ISelected = typeof selected;
    const onChange = (obj: Partial<ISelected>) => {
        SetSelected({
            ...selected,
            ...obj
        })
    }
    const submit = () => {
        if (addRecord(selected)) {
            alert("保存成功 (๑•̀ㅂ•́)و✧")
            SetSelected(defaultFormDate)
        };

    }

    return (
        <MyLayOut scrollTop={9999}>
            <TagsSection value={selected.tagIds} onChange={
                (tagIds) => onChange({ tagIds })
            } />
            <NotesSection value={selected.note} onChange={
                (note) => onChange({ note })
            } />
            <CategoryWrapper>
                <CategorySection value={selected.category} onChange={
                    (category) => onChange({ category })
                } />
            </CategoryWrapper>

            <NumberpadSection value={selected.amount} onChange={
                (amount) => onChange({ amount })
            }
                onOk={submit}
            />
        </MyLayOut>
    );
}

export default Money;