import styled from "styled-components";
import React from 'react'
import useTags from "hooks/useTags";


const Wrapper = styled.section`
    background:#ffffff;
    padding:12px 16px;
    display: flex;
    justify-content:flex-end;
    align-items:flex-start;
    flex-direction:column;
    flex-grow:1;
    >ol{
        margin:0 -12px;
    >li{
        background:#D9D9D9;
        border-radius:18px;
        display:inline-block;
        padding:3px 18px;
        font-size:14px;
        margin:8px 12px;
        &.selected{
            background-color: pink;
        }
    }
    }
    >button{
        margin-top:8px;
        background:none;
        border:none;
        padding:2px 4px;
        border-bottom:1px solid #333;
        color:#666;
    }
`
type IProps = {
    value: number[];
    onChange: (selected: number[]) => void;
}

const TagsSection: React.FC<IProps> = (props: IProps) => {
    // const [tag, SetTag] = useState<string[]>(["衣", "食", "住", "行"]);
    const { tag,onaddTag } = useTags();
    // const [selectedTags, SetSelectedTags] = useState<string[]>([]);
    const selectedTagIds = props.value;
    
    const onToggleTags = (tagID: number) => {
        const index = selectedTagIds.indexOf(tagID);
        if (index >= 0) {
            //如果tag已经被选中，就复制所有没有被选中的tag作为新的selectedTags
            //就是在被选过的里面除去又被选过的，selected里面永远是已经选过的
            props.onChange(selectedTagIds.filter(t => t !== tagID));
        } else {
            props.onChange([...selectedTagIds, tagID])
        }
    }

    return (
        <Wrapper>
            <ol>
                {tag.map((item) => {
                    return (
                        <li key={item.id} onClick={() => { onToggleTags(item.id); }}
                            className={selectedTagIds.indexOf(item.id) >= 0 ? "selected" : ""}
                        >{item.name}</li>
                    )
                })}
            </ol>
            <button onClick={onaddTag}>新增标签</button>
        </Wrapper>
    )
}


export default TagsSection;