import styled from "styled-components";

const Wrapper = styled.section`
    display:flex;
    flex-direction:column;
    >.output{
        background-color: #fff;
        font-size:36px;
        line-height:72px;
        text-align:right;
        padding:0 16px;
        box-shadow:inset 0 -2px 5px -2px #f3d6ec,
                    inset 0 2px 5px -2px #f3d6ec;
    }
    >.pad{
        button{
            font-size:18px;
            float:left;
            width:25%;
            height:64px;
            border:none;
            
            transition-duration: 0.1s;
            text-decoration: none;
            /* text-transform: uppercase;  */
            
            &.ok{
                height:128px;
                float:right;
            }
            &.zero{
                width:50%
            }
            &:nth-child(1){
                background: #f8f4f4;
            }
            &:nth-child(2),
            &:nth-child(5){
                background: #f7e8e8;
            }
            &:nth-child(3),
            &:nth-child(6),
            &:nth-child(9){
                background: #fae2e2;
            }
            &:nth-child(4),
            &:nth-child(7),
            &:nth-child(10){
                background: #fddada;
            }
            &:nth-child(8),
            &:nth-child(11),
            &:nth-child(13){
                background: #facfcf;
            }
            &:nth-child(12){
                background: #fdbaba;
            }
            &:nth-child(14){
                background: #f1c7c7;
            }
            
            &:active {
            background-color: #fab3b3;
            color: #000000;
            }
        }
    }
`
export default Wrapper;